import { useState, useRef } from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls, AlignmentToolbar, BlockControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, PanelRow, TextControl, __experimentalNumberControl as NumberControl, ToggleControl, RangeControl, SelectControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';
import produce from 'immer';

// Settings Components
import { Label, BColor, BtnGroup, ColorsControl, HelpPanel, InlineMediaUpload, SpaceControl, Typography } from '../../Components';
import { tabController } from '../../Components/utils/functions';
import { pxUnit, perUnit, emUnit } from '../../Components/utils/options';

import { BControlPro } from '../bplugins_sdk/src/components/v1';
import ProModal from './Components/ProModal';
import options from './utils/options';
const { modes, directions, interactivities, aligns, generalStyleTabs } = options;

const Settings = ({ attributes, setAttributes, fileFrameSize }) => {
	const { isPremium, playerAlign, file, isControls, isAutoplay, isLoop, isHover, mode, direction, count, speed, intermission, link, isLinkInNewTab, interactivity, interactivityContainer, interactivityActions, width, background, controlsHeight, controlsBG, controlsIconColor, controlsIconHoverColor, controlsIconActiveColor, controlsTrackColor, controlsThumbColor, isCap, caption, capAlign, capTypo, capColors, capPadding, capMargin } = attributes;

	const [proModalOpen, setProModalOpen] = useState(false);
	const tooltip = useRef(null);

	const currentPostType = wp.data.select('core/editor').getCurrentPostType();
	const currentPostId = wp.data.select('core/editor').getCurrentPostId();

	const handleShortcode = e => {
		const input = e.target;
		input.select();
		input.setSelectionRange(0, 30);
		document.execCommand('copy');
		tooltip.current.innerHTML = __('Copied Successfully!', 'lottie-player');
		setTimeout(() => {
			tooltip.current.innerHTML = __('Copy To Clipboard', 'lottie-player');
		}, 1500);
	};

	const updateInteractivityAction = (index, type, val, otherIndex = false) => {
		const newInterAction = [...interactivityActions];

		if (false !== otherIndex) {
			newInterAction[index][type][otherIndex] = val;
			setAttributes({ interactivityActions: newInterAction });
		} else {
			newInterAction[index][type] = val;
			setAttributes({ interactivityActions: newInterAction });
		}
	}

	const premiumProps = {
		isPremium,
		setOpen: setProModalOpen
	};

	const panelBodyIF = {
		className: 'bPlPanelBody',
		initialOpen: false
	}

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' == tab.name && <>
					{currentPostType === 'lpb' && <PanelBody className='bPlPanelBody'>
						<Label className='mb5'>Copy the shortcode and use it anywhere.</Label>
						<PanelRow>
							<div className='lpbFrontShortcode'>
								<input onClick={handleShortcode} value={`[lpb id=${currentPostId}]`} />
								<span ref={tooltip} className='tooltip'>
									{__('Copy To Clipboard', 'lottie-player')}
								</span>
							</div>
						</PanelRow>
					</PanelBody>}


					<HelpPanel slug='embed-lottie-player' docsLink='https://bplugins.com/docs/lottie-player' />


					<PanelBody className='bPlPanelBody' title={__('Player', 'lottie-player')}>
						<Label className='mb5'>{__('Lottie file url:', 'lottie-player')}</Label>
						{isPremium ? <InlineMediaUpload value={file} types={['application/json']} onChange={val => setAttributes({ file: val })} placeholder={__('Enter lottie file url', 'lottie-player')} /> : <TextControl value={file} onChange={val => setAttributes({ file: val })} />}
						<small>{__('Support only .json or .lottie file!', 'lottie-player')}</small>

						<ToggleControl className='mt20' label={__('Show Controls', 'lottie-player')} checked={isControls} onChange={val => setAttributes({ isControls: val })} />

						<ToggleControl className='mt10' label={__('Enable Autoplay', 'lottie-player')} checked={isAutoplay} onChange={val => setAttributes({ isAutoplay: val })} />

						<ToggleControl className='mt10' label={__('Enable Loop', 'lottie-player')} checked={isLoop} onChange={val => setAttributes({ isLoop: val })} />

						<BControlPro className='mt10' label={__('Enable Hover', 'lottie-player')} checked={isHover} onChange={val => setAttributes({ isHover: val })} {...premiumProps} Component={ToggleControl} />

						<BControlPro className='mt20' label={__('Mode:', 'lottie-player')} value={mode} onChange={val => setAttributes({ mode: val })} options={modes} {...premiumProps} Component={BtnGroup} />

						<BControlPro className='mt20' label={__('Direction:', 'lottie-player')} value={direction} onChange={val => setAttributes({ direction: parseInt(val) })} options={directions} {...premiumProps} Component={BtnGroup} />

						<BControlPro className='mt20' label={__('Count (Set 0 to infinite):', 'lottie-player')} value={count} onChange={val => setAttributes({ count: val })} min={0} max={50} step={1} {...premiumProps} Component={RangeControl} />

						<BControlPro className='mt20' label={__('Speed:', 'lottie-player')} value={speed} onChange={val => setAttributes({ speed: val })} min={0.01} max={20} step={.01} {...premiumProps} Component={RangeControl} />

						<BControlPro className='mt20' label={__('Interval (s):', 'lottie-player')} value={intermission} onChange={val => setAttributes({ intermission: val })} min={0} max={20} step={.01} {...premiumProps} Component={RangeControl} />

						<Label>{__('Link the player:', 'lottie-player')}</Label>
						<TextControl value={link} onChange={val => setAttributes({ link: val })} />
						<small>{__('If you want to link the player, enter the link here. Otherwise, leave as blank.', 'lottie-player')}</small>

						<BControlPro className='mt20' label={__('Open link in a new tab', 'lottie-player')} checked={isLinkInNewTab} onChange={val => setAttributes({ isLinkInNewTab: val })} {...premiumProps} Component={ToggleControl} />
					</PanelBody>


					<PanelBody title={__('Interactivity', 'lottie-player')} {...panelBodyIF}>
						<BControlPro label={__('Interactivity:', 'lottie-player')} value={interactivity} onChange={val => {
							setAttributes({ interactivity: val });
							'noInteractivity' === val && setAttributes({
								interactivityMode: '',
								interactivityActions: []
							});
							'syncWithScroll' === val && setAttributes({
								interactivityMode: 'scroll',
								interactivityActions: [
									{
										visibility: [0, 1],
										type: 'seek',
										frames: [0, fileFrameSize]
									}
								]
							});
							'scrollRelativeToContainer' === val && setAttributes({
								interactivityMode: 'scroll',
								interactivityContainer: '.wp-block-lpb-lottie-player',
								interactivityActions: [
									{
										visibility: [0, 1],
										type: 'seek',
										frames: [0, fileFrameSize]
									}
								]
							});
							'scrollWithOffset' === val && setAttributes({
								interactivityMode: 'scroll',
								interactivityActions: [
									{
										visibility: [0, 0.3],
										type: 'stop',
										frames: [0]
									},
									{
										visibility: [0.3, 1.0],
										type: 'seek',
										frames: [0, fileFrameSize]
									}
								]
							});
							'syncAnimationWithCursorPosition' === val && setAttributes({
								interactivityMode: 'cursor',
								interactivityActions: [
									{
										position: { x: [0, 1], y: [0, 1] },
										type: 'seek',
										frames: [0, fileFrameSize]
									}
								]
							});
							'syncAnimationWithCursorHorizontalMovement' === val && setAttributes({
								interactivityMode: 'cursor',
								interactivityActions: [
									{
										position: { x: [0, 1], y: [-1, 2] },
										type: 'seek',
										frames: [0, fileFrameSize]
									}
								]
							});
							'playAnimationOnClick' === val && setAttributes({
								interactivityMode: 'cursor',
								interactivityActions: [
									{
										type: 'click',
										forceFlag: false
									}
								]
							});
							'toggleAnimation' === val && setAttributes({
								interactivityMode: 'cursor',
								interactivityActions: [
									{
										type: 'toggle'
									}
								]
							});
							'playAnimationWhenVisible' === val && setAttributes({
								interactivityMode: 'scroll',
								interactivityActions: [
									{
										visibility: [0.50, 1.0],
										type: 'play'
									}
								]
							});
						}} options={interactivities} {...premiumProps} Component={SelectControl} />

						<small>{__('Upload file first then change interactivity option.', 'lottie-player')}</small>
						<br />
						<small>{__('Interactivity will not working in backend(editor).', 'lottie-player')}</small>

						{'scrollRelativeToContainer' === interactivity && <>
							<Label>{__('Container Selector', 'lottie-player')}</Label>
							<TextControl value={interactivityContainer} onChange={val => setAttributes({ interactivityContainer: val })} />

							<small>{__(`Enter container selector. Ex: if id enter '#id', if class '.class' or if tag 'tag name'`, 'lottie-player')}</small>
						</>}

						{'scrollWithOffset' === interactivity && <>
							<NumberControl className='mt20' label={__('Offset (%)', 'lottie-player')} labelPosition='left' value={interactivityActions?.[0]?.visibility?.[1] * 100} onChange={val => {
								updateInteractivityAction(0, 'visibility', parseFloat(val) / 100, 1);
								updateInteractivityAction(1, 'visibility', parseFloat(val) / 100, 0);
							}} />

							<small>{__(`Scroll animation when lottie player reached at ${interactivityActions?.[0]?.visibility?.[1] * 100} % of viewport`, 'lottie-player')}</small>
						</>}

						{'playAnimationWhenVisible' === interactivity && <>
							<NumberControl className='mt20' label={__('Offset (%)', 'lottie-player')} labelPosition='left' value={interactivityActions?.[0]?.visibility?.[0] * 100} onChange={val => updateInteractivityAction(0, 'visibility', parseFloat(val) / 100, 0)} />

							<small>{__(`Play animation when lottie player reached at ${interactivityActions?.[0]?.visibility?.[0] * 100} % of viewport`, 'lottie-player')}</small>
						</>}
					</PanelBody>


					<PanelBody title={__('Caption', 'lottie-player')} {...panelBodyIF}>
						<BControlPro label={__('Show Caption', 'lottie-player')} checked={isCap} onChange={val => setAttributes({ isCap: val })} {...premiumProps} Component={ToggleControl} />

						{isCap && <BControlPro className='mt20' label={__('Text:', 'lottie-player')} value={caption} onChange={val => setAttributes({ caption: val })} {...premiumProps} Component={TextControl} />}
					</PanelBody>
				</>}


				{'style' == tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Player', 'lottie-player')}>
						<UnitControl label={__('Width:', 'lottie-player')} labelPosition='left' value={width} onChange={val => setAttributes({ width: val })} units={[pxUnit(), perUnit(), emUnit()]} />

						<BColor label={__('Background Color:', 'lottie-player')} value={background} onChange={val => setAttributes({ background: val })} defaultColor='#0000' />
					</PanelBody>


					{isControls && <PanelBody title={__('Controls', 'lottie-player')}{...panelBodyIF}>
						<UnitControl label={__('Height:', 'lottie-player')} labelPosition='left' value={controlsHeight} onChange={val => setAttributes({ controlsHeight: val })} units={[pxUnit(), perUnit(), emUnit()]} />

						<BColor label={__('Background Color:', 'lottie-player')} value={controlsBG} onChange={val => setAttributes({ controlsBG: val })} defaultColor='#0000' />

						<BColor label={__('Icons Color:', 'lottie-player')} value={controlsIconColor} onChange={val => setAttributes({ controlsIconColor: val })} defaultColor='#4527a4' />

						<BColor label={__('Icons Hover Color:', 'lottie-player')} value={controlsIconHoverColor} onChange={val => setAttributes({ controlsIconHoverColor: val })} defaultColor='#8344c5' />

						<BColor label={__('Icons Active Color:', 'lottie-player')} value={controlsIconActiveColor} onChange={val => setAttributes({ controlsIconActiveColor: val })} defaultColor='#8344c5' />

						<BColor label={__('Track Color:', 'lottie-player')} value={controlsTrackColor} onChange={val => setAttributes({ controlsTrackColor: val })} defaultColor='#8344c5' />

						<BColor label={__('Thumb Color:', 'lottie-player')} value={controlsThumbColor} onChange={val => setAttributes({ controlsThumbColor: val })} defaultColor='#4527a4' />
					</PanelBody>}


					{isCap && <PanelBody title={__('Caption', 'lottie-player')} {...panelBodyIF}>
						<BControlPro label={__('Text Align:', 'lottie-player')} value={capAlign} onChange={val => setAttributes({ capAlign: val })} options={aligns} isIcon={true} size={isPremium ? 'medium' : 'small'} {...premiumProps} Component={BtnGroup} />

						<BControlPro label={__('Typography:', 'lottie-player')} value={capTypo} onChange={val => setAttributes({ capTypo: val })} defaults={{ fontSize: { desktop: 14, tablet: 14, mobile: 14 } }} produce={produce} {...premiumProps} Component={Typography} />

						<BControlPro label={__('Colors:', 'lottie-player')} value={capColors} onChange={val => setAttributes({ capColors: val })} {...premiumProps} Component={ColorsControl} />

						<BControlPro className='mt20' label={__('Padding:', 'lottie-player')} value={capPadding} onChange={val => setAttributes({ capPadding: val })} defaults={{ vertical: '10px', horizontal: '10px' }} {...premiumProps} Component={SpaceControl} />

						<BControlPro className='mt20' label={__('Margin:', 'lottie-player')} value={capMargin} onChange={val => setAttributes({ capMargin: val })} defaults={{ side: 4, top: '5px' }} {...premiumProps} Component={SpaceControl} />
					</PanelBody>}
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<ProModal proModalOpen={proModalOpen} setProModalOpen={setProModalOpen} />


		<BlockControls>
			<AlignmentToolbar value={playerAlign} onChange={val => setAttributes({ playerAlign: val })} describedBy={__('Player Alignment')} alignmentControls={[
				{ title: __('Player in left', 'lottie-player'), align: 'left', icon: 'align-left' },
				{ title: __('Player in center', 'lottie-player'), align: 'center', icon: 'align-center' },
				{ title: __('Player in right', 'lottie-player'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>
	</>;
};
export default Settings;